import React, { useState, useContext, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useNavigate, Link } from 'react-router-dom'; // useNavigate  import
import LoginContext from './LoginContext';
import NaverApi from './NaverApi';
import GoogleLogin from './GoogleLogin';

const Login = () => {
  const { loginMember, setLoginMember } = useContext(LoginContext);
  const [memberId, setMemberId] = useState("");
  const [memberPw, setMemberPw] = useState("");
  const [saveId, setSaveId] = useState(false); //     : false    : true
  const navigate = useNavigate(); // useNavigate  
  
  //      
  useEffect(() => { //      
                //  ()   localStorage.removeItem('savedId');
    const savedId = localStorage.getItem('savedId'); // localStorage saveId  
    if (savedId) {  //-> saveId      
      setMemberId(savedId); //  saveId     .
      setSaveId(true);
    }
  }, []);

  //  
  const loginButton = () => {
    
    fetch("/member-Login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ memberId, memberPw })
    })
    .then(response => {
      if (!response.ok) {
        throw new Error('response was not ok');
      }
      return response.json();
    })
    .then(data => {
      if(data.loginMember) {
        setLoginMember(data.loginMember);
        console.log('setLoginMember : ' , data.loginMember);
        localStorage.setItem('loginMember', JSON.stringify(data.loginMember));
        
        if (saveId) { //        save true 
          localStorage.setItem('savedId', memberId); // localStorage  key "saveId"  , value memberId   .
        } else {
          localStorage.removeItem('savedId'); //   saveId()   . false 
        }
        navigate('/'); //login redirect

      } else {
        alert(' .');
      }
    })
    .catch(error => {
      console.error('Fetch error:', error);
      alert('    .');
    });
  };




  return (
    <>
    <h1 className="title-ID">  .</h1>

    

    <div className="login-container">
      {/* loginMember  null  View */}
      <div className='login-body'>
      <h5 className='login-message'>      .</h5>
      {!loginMember && (
        <>
          
          <div className="input-value">
            <input
              type="text"
              value={memberId}
              onChange={(e) => setMemberId(e.target.value)}
              placeholder=" ."
            />
          </div>

          <div className="input-value">
            <input
              type="password"
              value={memberPw}
              onChange={(e) => setMemberPw(e.target.value)}
              placeholder=" ."
            />
          </div>

          <div className="input-save">
            <span>
              <input
                type="checkbox"
                checked={saveId} //  value   .
                onChange={e => setSaveId(e.target.checked)} //   setSaveId  
              /> 
              <label> </label>
            </span>
          </div>

          <div className="input-value">
            <button onClick={loginButton} className="btn btn-dark"></button>
          </div>

          <div className="input-value">
            <NaverApi />
            
          </div>

          <div className="input-value">
            <GoogleLogin/>
          </div>

          <div className='List'>
            <Link to="/memberIdFind"> </Link> 
            <p> | </p> 
            <Link to="/passwordFind">  </Link> 
            <p> | </p> 
            <Link to="/registerCheck"> </Link>
          </div>
        </>
      )}
      </div>
    </div>
   </>
  );
};

export default Login;