import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import '../css/NoticeWrite.css';

const NoticeWrite = () => {
   const [title, setTitle] = useState('');
   const [content, setContent] = useState('');
   const navigate = useNavigate();

   const resistration = (e) => {
    e.preventDefault(); //     
      writeCompleted();
   }

   const writeCompleted = () => {
      const writeCompletedData = {
         postTitle: title,
         postContent: content,
         postCount: 0, //   
         postCreateDate: new Date() //  
      };

      axios.post("/api/writeCompleted", writeCompletedData)
      .then(response => {
         alert("  .");
         setTitle(''); //  
         setContent(''); //  
         navigate('/customerBoard'); //     
      })
      .catch(err => {
         alert(" .");
      });
   };

   return (
      <div className="notice-container mt-4">
         <h2 className='text-center mb-4'> </h2>
         <form onSubmit={resistration}>
            <div className='write'>
               <div>
               <input type='text' className='title_txt' placeholder=''
                   value={title} onChange={(e) => setTitle(e.target.value)}
                  required />
                  </div>
                  </div>
            <div>
               
               <textarea className='content_txt' placeholder=' '
                   value={content} onChange={(e) => setContent(e.target.value)}
                  required></textarea>
            </div>
            <div className='post_submit'>
            <button type="submit"></button>
            </div>
         </form>
      </div>
   );
};

export default NoticeWrite;
