import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

const MemberPasswordFind = () => {
    const [memberId, setMemberId] = useState("");
    const [memberBirth, setMemberBirth] = useState("");
    const [memberPhone, setMemberPhone] = useState("");
    const [operationKey, setOperationKey] = useState(false); // true    
    const [securityCode, setSecurityCode] = useState("");
    const [userInfo, setUserInfo] = useState(null);
    const [change, setChange] = useState(false);
    const navigate = useNavigate();
    
    // 
    const birthRegex = /^\d{6}-[1-4]$/;
    const phoneRegex = /^(01[016789])[-\s]?\d{3,4}[-\s]?\d{4}$/;

    //   
    const formatPhoneNumber = (value) => {
        //  !!
        const cleanedPhone = value.replace(/\D/g, '');

        //    
        if (cleanedPhone.length <= 3) {
            return cleanedPhone;
        }
        if (cleanedPhone.length <= 7) {
            return `${cleanedPhone.slice(0, 3)}-${cleanedPhone.slice(3)}`;
        }
        return `${cleanedPhone.slice(0, 3)}-${cleanedPhone.slice(3, 7)}-${cleanedPhone.slice(7, 11)}`;
    };

    //  
    const phoneHandleChange = (e) => {
        const formattedPhoneNumber = formatPhoneNumber(e.target.value);
        setMemberPhone(formattedPhoneNumber);
    };

    //---------------------------------------------     ------------------------------------------------
//    
const isDateValid = (birthDate) => {
    const [datePart, genderPart] = birthDate.split('-');
    
    //    
    if (datePart.length !== 6 || !/^\d{6}$/.test(datePart) || !/^[1-4]$/.test(genderPart)) {
        return false;
    }
    
    // , ,  
    const yy = parseInt(datePart.slice(0, 2), 10);
    const mm = parseInt(datePart.slice(2, 4), 10);
    const dd = parseInt(datePart.slice(4, 6), 10);
    
    // YY YYYY 
    const year = yy >= 0 && yy <= 99 ? (yy < 30 ? 2000 + yy : 1900 + yy) : yy;
    
    //    
    if (mm < 1 || mm > 12) return false; //  1~12  
    
    //     
    const daysInMonth = new Date(year, mm, 0).getDate();
    if (dd < 1 || dd > daysInMonth) return false; //      
    
    return true;
};
//   
const birthHandleChange = (e) => {
    let value = e.target.value;
    
    //  
    value = value.replace(/[^\d]/g, '');
    
    //  
    if (value.length > 6) {
        value = value.slice(0, 6) + '-' + value.slice(6, 7);
    }
    
    setMemberBirth(value);
}
// ----------------------------------------------------------------------------------------------------------------------------------------


        //     
        const memberCheck = () => {
        if (!memberId || !memberBirth || !memberPhone) {
            alert("  .");
            return;
        }

        if (!birthRegex.test(memberBirth)) {
            alert("   . (YYYY-MM-DD)");
            return;
        }
        if (!phoneRegex.test(memberPhone)) {
            alert("   .");
            return;
        }


        //  , ,      
        fetch("/memberInfo-Find", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json"
            },
            body: JSON.stringify({ memberId, memberBirth, memberPhone })
        })
        .then(response => {
            if (!response.ok) {
                throw new Error("  .");
            }
            return response.json();
        })
        .then(data => {
            if (data) {
                setUserInfo(data);
                alert("   .");
                setChange(true);
            } else {
                alert("  .");
            }
        })
        .catch(error => {
            console.error("Fetch error:", error);
            alert("  .");
        });
    }

    //           
    const sendCode = () => {
       console.log(userInfo.memberEmail);
        fetch("/auth/send-code", {
            method: "POST",
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            },
            body: new URLSearchParams({ email: userInfo.memberEmail })
        }) 
        //      
        .then(response => response.json())
        .then(data => {
            if (data.status === "success") {
                alert("  .");
                setOperationKey(true);
            } else {
                alert("   .");
            }
        })
        .catch(error => {
            console.error("Fetch error:", error);
            alert("Fetch error" + error);
        });
    }


    //    
    const submitSuccess = () => {
        if (!securityCode) {
            alert("   .");
            return;
        }
        //     Controller  .
        fetch("/auth/verify-code", {
            method: "POST",
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            },
            body: new URLSearchParams({
                email: userInfo.memberEmail,
                code: securityCode
            })
        })
        .then(response => response.json()) 
        .then(data => {   //  data     
            console.log("data status : " + data.status);
            if (data.status === "success") {
                alert(" .");
                navigate('/passwordChange', { state: { data: data } });
            } else {
                alert("   .");
            }
        })
        .catch(error => {
            console.error("Fetch error:", error);
            alert("   .");
        });
    }

    return (
        <div className='group'>
            {!change ? 
            (<div>
                    <div className="title-ID-1">
                        <h1 style={{"font-weight":"bold"}}> </h1>
                        <br/><br/>
                    <h6>  ?      .</h6>
                    </div>
                   
            </div>) 
            : 
            (<div>
                <div className='input-value'>
                    <h1 className="title-ID"> </h1>
                </div>
                <div className='input-value'>
                    <h6 className="ID-messege">  ?      .</h6>
                </div>
            </div>) }

            {!change ? 
            (<div>
                <div className='login-container3'>
                    <h2 className="login-PwInfo"> </h2>
                    <h5 className="pw-message">    .</h5>
                    <div className="input-value">
                        <input
                            type="text"
                            value={memberId}
                            onChange={(e) => setMemberId(e.target.value)}
                            placeholder=" ."
                        />
                    </div>
                    <div className="input-value">
                        <input
                            type="text"
                            value={memberBirth}
                            onChange={birthHandleChange}
                            placeholder=" 7  / (-)  "
                        />
                    </div>
                    <div className="input-value">
                        <input
                            type="text"
                            value={memberPhone}
                            onChange={phoneHandleChange}
                            placeholder=" ."
                        />
                    </div>
                    <div className="insert-user-check">
                        <button className="btn btn-dark" onClick={memberCheck}>  </button>
                    </div>
                </div>
            </div>) 
            : 
            (<div>
                <div className='login-container4'>
                    
                    <h3 className="Pw-sTitle">     .</h3>
                    <h3 className="Pw-user"><u>{userInfo.memberName}</u>!</h3>
                    <h6 className="messagePw">   .</h6>
                    <u><h3 className="Pw-user">{userInfo.memberEmail}</h3></u>
                    

                    {!operationKey ? 
                    (<div className="input-value">
                        <button className="btn btn-dark" onClick={sendCode}> </button>
                    </div>) 
                    : 
                    (<div>
                        <input type="text" value={securityCode} onChange={(e) => setSecurityCode(e.target.value)} placeholder=" ."/>
                        <br/>
                        <button className="btn btn-dark submit" onClick={submitSuccess}> </button>
                    </div>)}
                </div>
            </div>)}
        </div>
    );
}

export default MemberPasswordFind;