import React from 'react';
import { Link } from "react-router-dom";
import "./css/StaticNavbar.css"; //   

function StaticNavbar() {
  return (
    <div className="static-all">
      <nav className="static-sor">
        <Link to="/Moviechart" className="static staticmovie"></Link>
        <Link to="/Movieboard-app" className="static staticbooking"></Link>
        <div className="staticone staticbrand">
          <Link to="/">Sixsence</Link>
        </div>
        <Link to="/store" className="static staticstore"></Link>
        <Link to="/CustomerBoard" className="static staticborder"></Link>
      </nav>
    </div>
  );
}

export default StaticNavbar;