import React,{useState, useEffect} from "react";
import {useNavigate, useParams} from "react-router-dom";
import axios from "axios";
import '../css/AdminAnswer.css';

const AdminAnswer = () => {
   const { oboNo } = useParams();
   const [obo, setObo] = useState(null);
   const [oboAnswer, setOboAnswer] = useState('');
   const [isAnswering, setIsAnswering] = useState(false);  //    
   const navigate = useNavigate();

   // 1:1  
   useEffect(() => {
    axios.get(`/api/oboList/${oboNo}`)
    .then(response => {
      setObo(response.data);
    })
    .catch(e=> alert("  ."));
   } ,[oboNo]);

   //  
   const handleAnswerSubmit = () => {
     const answerData = {
       ...obo,  //   
       oboAnswer: oboAnswer,  //  
       oboStatus: 'y'  //  'y' 
     };

     axios.post("/api/registerAnswer", answerData)
     .then(response => {
       alert(" .");
       navigate("/AdminObo");  //     
     })
     .catch(error => {
       alert("  .");
       console.error("", error);
     });
   };
   
  // 
   const oboDelete = () => {
    axios.delete(`/api/obo/${oboNo}`)
    .then(() => {
      alert("");
      navigate("/AdminObo"); 
    })
    .catch(error => {
      console.error("  .", error);
      alert(" .");
    });
  };
   
   return(
    <div className="admin-answer-container">
    {obo ? (
      <>
        <p>: {obo.memberName}</p>
        <p>: {obo.oboTitle}</p>
        <p>: {obo.oboContent}</p>
        {isAnswering ? (
          <>
            <textarea
              type="text" 
              value={oboAnswer} 
              onChange={(e) => setOboAnswer(e.target.value)}
              placeholder=" " 
            />
            <button  onClick={handleAnswerSubmit}> </button>
            <button size="large" onClick={() => navigate("/AdminObo")}></button>
            
          </>
        ) : (
          <button onClick={() => setIsAnswering(true)}></button>
          
        )}
        <button onClick={oboDelete}></button>
      </>
    ) : (
      <p> ...</p>
    )}
  </div>
   )
};

export default AdminAnswer;
