import { useState, useEffect } from "react";

const AddressSearch = ({ onAddressChange }) => {
  const [address, setAddress] = useState(""); //  
  const [additionalAddress, setAdditionalAddress] = useState(""); //  
  const [finalAddress, setFinalAddress] = useState(""); //  

  //      
  const handleComplete = (data) => {
    let fullAddress = data.address; //  
    let extraAddress = ""; //  

    if (data.addressType === "R") { //   
      if (data.bname) {
        extraAddress += data.bname;
      }
      if (data.buildingName) {
        extraAddress += extraAddress ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress ? ` (${extraAddress})` : "";
    }

    setAddress(fullAddress);
  };

  //         
  const openPostcode = () => {
    new window.daum.Postcode({
      oncomplete: handleComplete,
    }).open();
  };

  //         
  useEffect(() => {
    const combinedAddress = `${address} , [${additionalAddress}]`;
    setFinalAddress(combinedAddress);
    onAddressChange(combinedAddress); //     
  }, [address, additionalAddress, onAddressChange]);

  return (
    <div>
      <button className="btn btn-dark" onClick={openPostcode}> </button>
      {address && (
        <div>
          <input
            type="text"
            placeholder="   ( :   / )"
            value={additionalAddress}
            onChange={(e) => setAdditionalAddress(e.target.value)}
          />
          <div> : {address}</div>
        </div>
      )}
      {address && additionalAddress && (
        <>
          <h5>{finalAddress}</h5>
          <input type="hidden" value={finalAddress} />
        </>
      )}
    </div>
  );
};

export default AddressSearch;