import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import { useNavigate, useParams } from 'react-router-dom';
import { FaRegStar, FaStar } from "react-icons/fa";
import "./css/MovieDetail.css";
import LoginContext from '../login/LoginContext';

const MovieDetail = () => {
    const { movieNo } = useParams();
    const [moviedata, setMovieData] = useState(null);
    const [soname, setSoname] = useState("");  //   
    const [review, setReview] = useState("");  //  
    const [reviews, setReviews] = useState([]);  //  
    const [rating, setRating] = useState(0);  // 
    const { loginMember } = useContext(LoginContext);  //   
    const navigate = useNavigate();

    useEffect(() => {
        axios.get(`/api/movie/selectMovie?movieNo=${movieNo}`)
            .then(response => {
                setMovieData(response.data[0]);
            })
            .catch(e => alert(" "));
    }, [movieNo]);

    useEffect(() => {
        axios.get(`/api/comment/movie/${movieNo}`)
            .then(response => {
                setReviews(response.data);
            })
            .catch(e => alert("  "));
    }, [movieNo]);

    //   
    const handleReviewSubmit = () => {
        if (!loginMember) {
            const shouldNavigate = window.confirm(" .   ?");
            if (shouldNavigate) {
                navigate('/memberLogin');
                return;
            } else {
                return;
            }
        }
        const newReview = {
            soname: loginMember.memberName || soname,
            text: review,
            rating: rating,
            memberNo: loginMember.memberNo
        };
        axios.post(`/api/comment/insert`, {
            movieNo: movieNo,
            text: newReview.text,
            score: newReview.rating,
            soname: newReview.soname,
            memberNo: newReview.memberNo
        })
        .then(response => {
            //    ID .
            const addedReview = {
                ...newReview,
                coid: response.data.coid  //    ID
            };
            //         .
            axios.get(`/api/comment/movie/${movieNo}`)
                .then(res => {
                    setReviews(res.data);
                })
                .catch(e => alert("  "));
            setReview("");
            setRating(0);
        })
        .catch(e => alert("  "));
    };

    //   
    const handleDeleteReview = (coid, reviewMemberNo) => {
        if (loginMember.memberNo !== reviewMemberNo) {
            alert("     .");
            return;
        }

        axios.delete(`/api/comment/delete?coid=${coid}`)
        
        .then(response => {
            console.log("Review deleted successfully");
            setReviews(prevReviews => prevReviews.filter(review => review.coid !== coid));
        })
        .catch(error => {
            console.error("Error deleting review:", error);
        });
    };

    //   
    const handleRatingClick = (value) => {
        setRating(value);
    };

    useEffect(() => {
        if (loginMember) {
            setSoname(loginMember.memberName); //    
        }
    }, [loginMember]);

    if (!moviedata) {
        return <div> ...</div>;
    }

    return (
        <div className='detail-container'>
            <div className="movie-header">
                <img className='image' src={`.${moviedata.movieImage}`} alt={moviedata.movieTitle} />
                <div className="movie-infomation">
                    <h1 className='title'>{moviedata.movieTitle}</h1>
                    <div className='grade'> : {moviedata.movieGrade}</div>
                    <div className='showtime'> : {moviedata.movieShowtime}</div>
                    <div className='story'>: {moviedata.movieStory}</div>
                    <a href={`/Movieboard-app?movieId=${movieNo}`} className="detail-button"></a>
                </div>
            </div>

            <div className='review-section'>
                <div className='star-rating'>
                    {[1, 2, 3, 4, 5].map(value => (
                        value <= rating ? 
                        <FaStar
                            key={value}
                            size={30}
                            className="star selected"
                            onClick={() => handleRatingClick(value)}
                        /> : 
                        <FaRegStar
                            key={value}
                            size={30}
                            className="star"
                            onClick={() => handleRatingClick(value)}
                        />
                    ))}
                </div>

                <div className='review-form'>
                    <input
                        type="text"
                        value={soname}
                        onChange={(e) => setSoname(e.target.value)}
                        placeholder=" "
                        className="soname-input"
                        disabled={!!loginMember}  //     
                    />
                    <textarea
                        value={review}
                        onChange={(e) => setReview(e.target.value)}
                        placeholder=" "
                        className="review-textarea"
                    />
                    <button className="review-submit-button" onClick={handleReviewSubmit}></button>
                </div>
            </div>
            <div className='reviews-list'>
                <h2></h2>
                {reviews.length === 0 ? (
                    <p>   .</p>
                ) : (
                    <ul>
                        {reviews.map((rev, index) => (
                            <li key={index} className="review-item">
                                <strong>{rev.soname}:</strong> {rev.text} ({rev.score})
                                {loginMember && loginMember.memberNo === rev.memberNo && (
                                    <button 
                                        className="delete-button" 
                                        onClick={() => handleDeleteReview(rev.coid, rev.memberNo)}
                                    >
                                        
                                    </button>
                                )}
                            </li>
                        ))}
                    </ul>
                )}
            </div>
        </div>
    );
}

export default MovieDetail;