import React, { useState, useEffect, useContext } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import LoginContext from "../login/LoginContext";
import "../css/NoticeView.css";

const NoticeView = () => {
  const { loginMember } = useContext(LoginContext);  //   
  const { postNo } = useParams(); 
  const [post, setPost] = useState(null); //     
  const navigate = useNavigate(); //    navigate 
  const [editPost, setEditPost] = useState({
    postTitle:"",
    postContent:"",
    postCreateDate:"",
    postCount:""
  });

  //        
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    axios.get(`/api/board/${postNo}`)
      .then(response => {
        setPost(response.data);
      })
      .catch(error => {
        console.log("", error);
      });
  }, [postNo]);

  if (!post) {
    return <div>Loading...<img src="/board_image/b_image.jpg"/></div>;
 }

  //    
  const isAdmin = loginMember && loginMember.memberNo === 1;

  //     
  const handleSaveChanges = () => {
    axios.put(`/api/boardEdit/${postNo}`, editPost)
      .then(response => {
        setPost(response.data); // DB    
        setIsEditing(false); //   
        navigate('/CustomerBoard'); //     NoticeView  ( )
      })
      .catch(error => {
        console.error("   .", error);
      });
  };

  //  
   const noticeDelete = () => {
    axios.delete(`/api/post/${postNo}`)
    .then(() => {
      alert(" .");
      navigate("/CustomerBoard")
    })
    .catch(error => {
      console.error("  .", error);
      alert(" .");
    });
   };

  return (
    <div className="post-container mt-4">
      {isEditing ? (
        //     
        <div className="post-edit-container">
          <p>:{post.postTitle}</p>
          <p>:{post.postContent}</p>
          <input 
            type="text" 
            name="postTitle" 
            value={editPost.postTitle}
            onChange={(e) => setEditPost({...editPost, postTitle: e.target.value})} 
            placeholder=""
          />
          <textarea 
            name="postContent" 
            value={editPost.postContent}
            onChange={(e) => setEditPost({...editPost, postContent: e.target.value})} 
            placeholder=""
          />
          <button onClick={handleSaveChanges}></button>
        </div>
 ) : (
  //      
  <div>
    <h2>{post.postTitle}</h2>
    <p>{post.postContent}</p>
    <p><strong>:</strong> {post.postCreateDate}</p>
    <p className="post-count"><strong>:</strong> {post.postCount}</p>
    <button size="large" onClick={() => navigate("/CustomerBoard")}></button>
    {/*      */}
    {isAdmin && (
      <button onClick={() => setIsEditing(true)}></button>
    )}
    {isAdmin && (
      <button onClick={noticeDelete}></button>
    )}
  </div>
)}
</div>
);
};

export default NoticeView;