import { useNavigate, useLocation, Link  } from "react-router-dom";
import React, {useState} from "react";


const MemberPwChange = () => {

    const location = useLocation();
    const data = location.state?.data;  //   .

    const [memberPw, setMemberPw] = useState("");
    const [memberPwCheck, setMemberPwCheck] = useState("");
    const [change, setChange] = useState(false);
    const passwordRegex = /^(?=.*[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?])[A-Za-z0-9!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]{10,15}$/;
     //   <p>    
     const isPasswordValid = passwordRegex.test(memberPw) && passwordRegex.test(memberPwCheck);
     const arePasswordsMatching = memberPw === memberPwCheck;
     const isInputFilled = memberPw && memberPwCheck;
    
    
    
     //   
    const handlePasswordChange = () => {
        
        if (!memberPw || !memberPwCheck) {
            alert("  .");
            return;
        }
        
        if(memberPw ==! memberPwCheck){
            alert("  .");
            return;
        }                             // location   data data.status  success    ("memberEmail",email)      ..!!
        console.log("data  : " + data.memberEmail, "new password  : " + memberPw + " : " + memberPwCheck);
        
        fetch("/change-password", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json"
            },
            body: JSON.stringify({
                memberEmail: data.memberEmail,
                memberPw: memberPw,
                memberPwCheck : memberPwCheck
            })
        })
        .then(response => {
            if (!response.ok) {
                // HTTP     .
                throw new Error(`HTTP error! Status: ${response.status}`);
            }
            return response.json();
        })
        .then(data => {
            if (data) {
                alert("  .");
                setChange(true);
            } else {
                alert("  .");
            }
        })
        .catch(error => {
            console.error("Fetch error  :", error);
            alert("    . " + error.message);
        });
    }

    return (
        
        <div className='grop'>

        <div className='input-value'>
        <h1 className="title-ID"> </h1>
        </div>
        
        
           
       {!change ? (<div className='login-container4'>
        <div className='input-value'> 
        <h6 className="input-pwMessage1">    </h6>
        <h6 className="input-pwMessage1">      .</h6>
        </div>
        
       {/***************************************************************************  ********************************************************************************/ }
       <div>
            <input type="password" className="input-pwChange" value={memberPw} 
            onChange={e => setMemberPw(e.target.value)} placeholder=" ." required/>
        </div>
        
        <div >
            <input type="password" className="input-pwChange" value={memberPwCheck} 
            onChange={e => setMemberPwCheck(e.target.value)} placeholder="  ." required/>
        </div>
         
         {isInputFilled && (
            <>
                {isPasswordValid && arePasswordsMatching ? (
                    <p style={{ color: "green", margin: "0", fontSize: "13px" }}> .</p>
                ) : (
                    <>
                        {!arePasswordsMatching && (
                            <p style={{ color: "red", margin: "0", fontSize: "13px" }}>  .</p>
                        )}
                        {!isPasswordValid && (
                            <p style={{ color: "red", margin: "0", fontSize: "13px" }}>
                                 10 ~ 15 ,   .
                            </p>
                        )}
                    </>
                )}
            </>
        )}
        <button className="btn btn-dark input-pwChange1" onClick={handlePasswordChange }> </button>
        </div>) : 
        (   
            <div className="login-container4">
                <h6 className="input-pwMessage1">   .</h6>
                <h6 className="input-pwMessage1">  .</h6>

                <h2> .</h2>
                <div className="pw-button">
                <Link to="/"><button className="btn btn-dark"> </button></Link>
                <Link to="/mypageMain"><button className="btn btn-dark"> </button></Link>
                </div>
            </div>
        )}
                

        </div>
    )
}

export default MemberPwChange;




