import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from 'react-router-dom';
import '../css/AdminObo.css'

const AdminObo = () => {
  const [oboData, setOboData] = useState([]); //    
  const [currentPage, setCurrentPage] = useState(1); //    
  const [itemPerPage] = useState(6); //    

  useEffect(() => {
    axios.get("/api/oboList")
      .then(response => {
        setOboData(response.data);
        console.log("oboData:", oboData);
      }) 
      .catch(e => alert(" ."));
  }, []);

  //    
  const paginate = (pageNumber) => setCurrentPage(pageNumber); //     

  //   
  const lastItemIndex = currentPage * itemPerPage; //      
  const firstItemIndex = lastItemIndex - itemPerPage; //       
  const currentItems = oboData.slice(firstItemIndex, lastItemIndex); //     

  return (
    <div className="container-admin">
      <h1> </h1>
      <button className="admin-button"><Link to={"/CustomerBoard"}></Link></button>
      <div className="admin-table-responsive">
        <table>
        <thead>
        <tr >
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {currentItems.map(item => (
              <tr key={item.oboNo}>
                <td>{item.oboNo}</td>
                <td>{item.memberName}</td>
                <td>{item.oboMovieType === 'gangnam' ? '' : item.oboMovieType === 'yeoksam' ? '' : item.oboMovieType}</td>
                <td>{item.oboInquiryType === 'general' ? '' : item.oboInquiryType === 'technical' ? '' : item.oboInquiryType === 'payment' ? '' : item.oboInquiryType}</td>
                {/*   AdminAnswer   */}
                <td><Link to={`/AdminAnswer/${item.oboNo}`}>{item.oboTitle}</Link></td>
                <td>{item.oboCreateDate}</td>
                <td>{item.oboStatus === 'y' ? '' : item.oboStatus === 'n' ? '':item.oboStatus}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {/*  */}
      <Pagination
        itemPerPage={itemPerPage} //    
        totalItems={oboData.length} //    
        paginate={paginate} //    
        currentPage={currentPage} //   
      />
    </div>
  );
};

//   AdminObo  
const Pagination = ({ itemPerPage, totalItems, paginate, currentPage }) => {
  const pageNumbers = [];
  for (let i = 1; i <= Math.ceil(totalItems / itemPerPage); i++) {
    pageNumbers.push(i);
  }

  return (
    <nav>
      <ul className="pagination justify-content-center">
        {pageNumbers.map(number => (
          <li key={number} className={`page-item ${currentPage === number ? 'active' : ''}`}>
            {/*         */}
            <a onClick={(e) => { e.preventDefault(); paginate(number); }} href="!#" className='page-link'>
              {number}
            </a>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default AdminObo;
