import React from 'react';
import Modal from 'react-modal';
import "./MypageCss.css";

const MypageModal = ({ modalOpen, cancelList, handleButtonClick, handleComment }) => {
  const msg = () => {
    switch(handleComment) {
      case 'movie':
        return '  ?';
      case 'item':
        return '  ?';
      case 'delAccount':
        return '  ?';
      case 'obo':
        return ' ?';
      case 'editMember':
        return ' ?';
      case 'comment':
        return ' ?';
    }
  }

  return (
    <Modal
      isOpen={modalOpen}
      contentLabel="Example Modal" 
      style={{
        content: {
          top: "50%",
          left: "50%",
          right: "auto",
          bottom: "auto",
          marginRight: "-50%",
          transform: "translate(-50%, -50%)",
          width: "500px",
        },
      }}
    >
      <h2>{msg()}</h2>
      <div className='modalBtn'>
        <button className='mypageModal' onClick={() => handleButtonClick(cancelList)}></button>
        <button className='mypageModal' onClick={() => handleButtonClick("Cancel")}></button>
      </div>
    </Modal>
  );
};

export default MypageModal;